___CSS_LOADER_EXPORT___.push([module.id, `/* ==================== RESET & BASE STYLES ==================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Clear/Sunny Theme (Default) */
  --bg-gradient-start: #87ceeb;
  --bg-gradient-end: #4a90e2;
  --text-primary: #1f2937;
  --text-secondary: #4b5563;
  --text-light: #f9fafb;
  --card-bg: rgba(255, 255, 255, 0.9);
  --card-shadow: rgba(0, 0, 0, 0.1);
  --accent-color: #f59e0b;
  --accent-hover: #d97706;
  --error-color: #ef4444;
  --border-color: rgba(255, 255, 255, 0.3);
}

body {
  font-family:
    -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.6;
  background: linear-gradient(
    135deg,
    var(--bg-gradient-start),
    var(--bg-gradient-end)
  );
  min-height: 100vh;
  color: var(--text-primary);
  transition: background 0.5s ease;
}

/* ==================== WEATHER THEMES ==================== */
body.clear {
  --bg-gradient-start: #87ceeb;
  --bg-gradient-end: #4a90e2;
  --text-primary: #1f2937;
  --card-bg: rgba(255, 255, 255, 0.9);
}

body.cloudy {
  --bg-gradient-start: #b0bec5;
  --bg-gradient-end: #78909c;
  --text-primary: #1f2937;
  --card-bg: rgba(255, 255, 255, 0.85);
}

body.rain {
  --bg-gradient-start: #4a5568;
  --bg-gradient-end: #2d3748;
  --text-primary: #f9fafb;
  --text-secondary: #e5e7eb;
  --card-bg: rgba(45, 55, 72, 0.8);
  --border-color: rgba(255, 255, 255, 0.2);
}

body.snow {
  --bg-gradient-start: #e0f2fe;
  --bg-gradient-end: #bae6fd;
  --text-primary: #1e3a8a;
  --card-bg: rgba(255, 255, 255, 0.9);
}

body.night {
  --bg-gradient-start: #1a202c;
  --bg-gradient-end: #2c5282;
  --text-primary: #f9fafb;
  --text-secondary: #e5e7eb;
  --card-bg: rgba(26, 32, 44, 0.85);
  --border-color: rgba(255, 255, 255, 0.15);
}

/* ==================== LAYOUT ==================== */
.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* ==================== HEADER ==================== */
.header {
  text-align: center;
  margin-bottom: 2rem;
}

.app-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  text-shadow: 2px 2px 4px var(--card-shadow);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.app-title i {
  font-size: 2.5rem;
  color: var(--accent-color);
}

/* ==================== SEARCH SECTION ==================== */
.search-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
  align-items: center;
}

.search-form {
  width: 100%;
  max-width: 600px;
}

.search-input-group {
  display: flex;
  gap: 0.5rem;
  background: var(--card-bg);
  padding: 0.5rem;
  border-radius: 50px;
  box-shadow: 0 4px 6px var(--card-shadow);
  backdrop-filter: blur(10px);
}

.search-input {
  flex: 1;
  border: none;
  background: transparent;
  padding: 0.75rem 1.25rem;
  font-size: 1rem;
  color: var(--text-primary);
  outline: none;
}

.search-input::placeholder {
  color: var(--text-secondary);
  opacity: 0.7;
}

.search-btn {
  background: var(--accent-color);
  color: white;
  border: none;
  padding: 0.75rem 2rem;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.search-btn:hover {
  background: var(--accent-hover);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.4);
}

.search-btn:active {
  transform: translateY(0);
}

/* ==================== TEMPERATURE TOGGLE ==================== */
.temp-toggle {
  display: flex;
  gap: 0.5rem;
  background: var(--card-bg);
  padding: 0.5rem;
  border-radius: 50px;
  box-shadow: 0 4px 6px var(--card-shadow);
  backdrop-filter: blur(10px);
}

.toggle-btn {
  padding: 0.5rem 1.5rem;
  border: 2px solid transparent;
  background: transparent;
  color: var(--text-secondary);
  font-size: 1rem;
  font-weight: 600;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle-btn.active {
  background: var(--accent-color);
  color: white;
  border-color: var(--accent-color);
}

.toggle-btn:not(.active):hover {
  background: rgba(245, 158, 11, 0.1);
  border-color: var(--accent-color);
}

/* ==================== LOADING SPINNER ==================== */
.loading-spinner {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 3rem;
  color: var(--text-primary);
}

.loading-spinner.active {
  display: flex;
}

.spinner {
  width: 60px;
  height: 60px;
  border: 4px solid var(--border-color);
  border-top: 4px solid var(--accent-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* ==================== ERROR MESSAGE ==================== */
.error-message {
  display: none;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  background: var(--card-bg);
  border-radius: 20px;
  box-shadow: 0 10px 30px var(--card-shadow);
  backdrop-filter: blur(10px);
  max-width: 500px;
  margin: 2rem auto;
  text-align: center;
}

.error-message.active {
  display: flex;
}

.error-message i {
  font-size: 3rem;
  color: var(--error-color);
}

.error-text {
  color: var(--text-primary);
  font-size: 1.1rem;
}

.retry-btn {
  background: var(--accent-color);
  color: white;
  border: none;
  padding: 0.75rem 2rem;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.retry-btn:hover {
  background: var(--accent-hover);
  transform: translateY(-2px);
}

/* ==================== WEATHER DISPLAY ==================== */
.weather-display {
  display: none;
  gap: 2rem;
  flex: 1;
}

.weather-display.active {
  display: flex;
  flex-direction: column;
}

/* ==================== CURRENT WEATHER ==================== */
.current-weather {
  background: var(--card-bg);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 10px 30px var(--card-shadow);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-color);
}

.location-info {
  text-align: center;
  margin-bottom: 2rem;
}

.location-name {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.current-date {
  font-size: 1rem;
  color: var(--text-secondary);
}

.current-conditions {
  text-align: center;
  margin-bottom: 2rem;
}

.temp-display {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.weather-icon {
  font-size: 5rem;
  color: var(--accent-color);
}

.temp-info {
  display: flex;
  align-items: flex-start;
}

.current-temp {
  font-size: 5rem;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1;
}

.temp-unit {
  font-size: 2rem;
  color: var(--text-secondary);
  margin-left: 0.25rem;
}

.weather-description {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.feels-like {
  font-size: 1rem;
  color: var(--text-secondary);
}

/* ==================== WEATHER DETAILS GRID ==================== */
.weather-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 2px solid var(--border-color);
}

.detail-item {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.detail-item i {
  font-size: 2rem;
  color: var(--accent-color);
}

.detail-info {
  flex: 1;
}

.detail-label {
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
}

.detail-value {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
}

/* ==================== FORECAST SECTION ==================== */
.forecast-section {
  background: var(--card-bg);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 10px 30px var(--card-shadow);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-color);
}

.forecast-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  text-align: center;
}

.forecast-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1rem;
}

.forecast-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem;
  border-radius: 15px;
  text-align: center;
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.forecast-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px var(--card-shadow);
}

.forecast-day {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.forecast-icon {
  font-size: 2.5rem;
  color: var(--accent-color);
  margin: 0.5rem 0;
}

.forecast-temps {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  font-size: 1rem;
  color: var(--text-primary);
}

.forecast-high {
  font-weight: 600;
}

.forecast-low {
  color: var(--text-secondary);
}

/* ==================== FOOTER ==================== */
.footer {
  text-align: center;
  margin-top: 3rem;
  padding: 2rem 1rem;
  color: var(--text-primary);
  opacity: 0.8;
}

.footer p {
  margin: 0.5rem 0;
  font-size: 0.875rem;
}

.footer a {
  color: var(--accent-color);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease;
}

.footer a:hover {
  color: var(--accent-hover);
  text-decoration: underline;
}

/* ==================== RESPONSIVE DESIGN ==================== */
@media (max-width: 768px) {
  .app-title {
    font-size: 2rem;
  }

  .app-title i {
    font-size: 2rem;
  }

  .search-input-group {
    flex-direction: column;
    border-radius: 15px;
  }

  .search-input,
  .search-btn {
    width: 100%;
    border-radius: 10px;
  }

  .current-temp {
    font-size: 4rem;
  }

  .weather-icon {
    font-size: 4rem;
  }

  .temp-unit {
    font-size: 1.5rem;
  }

  .weather-details {
    grid-template-columns: repeat(2, 1fr);
  }

  .forecast-grid {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }
}

@media (max-width: 480px) {
  .app-container {
    padding: 1rem 0.5rem;
  }

  .app-title {
    font-size: 1.5rem;
    flex-direction: column;
    gap: 0.5rem;
  }

  .current-weather,
  .forecast-section {
    padding: 1.5rem 1rem;
  }

  .weather-details {
    grid-template-columns: 1fr;
  }
}
