EAC1B,qBAAqB;AACvB;;AAEA;EACE,iBAAiB;EACjB,0BAA0B;EAC1B,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,WAAW;EACX,eAAe;EACf,0BAA0B;AAC5B;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,4BAA4B;AAC9B;;AAEA,qDAAqD;AACrD;EACE,kBAAkB;EAClB,gBAAgB;EAChB,kBAAkB;EAClB,0BAA0B;EAC1B,YAAY;AACd;;AAEA;EACE,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,0BAA0B;EAC1B,qBAAqB;EACrB,gBAAgB;EAChB,2BAA2B;AAC7B;;AAEA;EACE,0BAA0B;EAC1B,0BAA0B;AAC5B;;AAEA,gEAAgE;AAChE;EACE;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,sBAAsB;IACtB,mBAAmB;EACrB;;EAEA;;IAEE,WAAW;IACX,mBAAmB;EACrB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,qCAAqC;EACvC;;EAEA;IACE,2DAA2D;EAC7D;AACF;;AAEA;EACE;IACE,oBAAoB;EACtB;;EAEA;IACE,iBAAiB;IACjB,sBAAsB;IACtB,WAAW;EACb;;EAEA;;IAEE,oBAAoB;EACtB;;EAEA;IACE,0BAA0B;EAC5B;AACF","sourcesContent":["/* ==================== RESET & BASE STYLES ==================== */\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\n:root {\r\n  /* Clear/Sunny Theme (Default) */\r\n  --bg-gradient-start: #87ceeb;\r\n  --bg-gradient-end: #4a90e2;\r\n  --text-primary: #1f2937;\r\n  --text-secondary: #4b5563;\r\n  --text-light: #f9fafb;\r\n  --card-bg: rgba(255, 255, 255, 0.9);\r\n  --card-shadow: rgba(0, 0, 0, 0.1);\r\n  --accent-color: #f59e0b;\r\n  --accent-hover: #d97706;\r\n  --error-color: #ef4444;\r\n  --border-color: rgba(255, 255, 255, 0.3);\r\n}\r\n\r\nbody {\r\n  font-family:\r\n    -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,\r\n    Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\r\n  line-height: 1.6;\r\n  background: linear-gradient(\r\n    135deg,\r\n    var(--bg-gradient-start),\r\n    var(--bg-gradient-end)\r\n  );\r\n  min-height: 100vh;\r\n  color: var(--text-primary);\r\n  transition: background 0.5s ease;\r\n}\r\n\r\n/* ==================== WEATHER THEMES ==================== */\r\nbody.clear {\r\n  --bg-gradient-start: #87ceeb;\r\n  --bg-gradient-end: #4a90e2;\r\n  --text-primary: #1f2937;\r\n  --card-bg: rgba(255, 255, 255, 0.9);\r\n}\r\n\r\nbody.cloudy {\r\n  --bg-gradient-start: #b0bec5;\r\n  --bg-gradient-end: #78909c;\r\n  --text-primary: #1f2937;\r\n  --card-bg: rgba(255, 255, 255, 0.85);\r\n}\r\n\r\nbody.rain {\r\n  --bg-gradient-start: #4a5568;\r\n  --bg-gradient-end: #2d3748;\r\n  --text-primary: #f9fafb;\r\n  --text-secondary: #e5e7eb;\r\n  --card-bg: rgba(45, 55, 72, 0.8);\r\n  --border-color: rgba(255, 255, 255, 0.2);\r\n}\r\n\r\nbody.snow {\r\n  --bg-gradient-start: #e0f2fe;\r\n  --bg-gradient-end: #bae6fd;\r\n  --text-primary: #1e3a8a;\r\n  --card-bg: rgba(255, 255, 255, 0.9);\r\n}\r\n\r\nbody.night {\r\n  --bg-gradient-start: #1a202c;\r\n  --bg-gradient-end: #2c5282;\r\n  --text-primary: #f9fafb;\r\n  --text-secondary: #e5e7eb;\r\n  --card-bg: rgba(26, 32, 44, 0.85);\r\n  --border-color: rgba(255, 255, 255, 0.15);\r\n}\r\n\r\n/* ==================== LAYOUT ==================== */\r\n.app-container {\r\n  max-width: 1200px;\r\n  margin: 0 auto;\r\n  padding: 2rem 1rem;\r\n  min-height: 100vh;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n/* ==================== HEADER ==================== */\r\n.header {\r\n  text-align: center;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.app-title {\r\n  font-size: 2.5rem;\r\n  font-weight: 700;\r\n  color: var(--text-primary);\r\n  text-shadow: 2px 2px 4px var(--card-shadow);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.app-title i {\r\n  font-size: 2.5rem;\r\n  color: var(--accent-color);\r\n}\r\n\r\n/* ==================== SEARCH SECTION ==================== */\r\n.search-section {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1rem;\r\n  margin-bottom: 2rem;\r\n  align-items: center;\r\n}\r\n\r\n.search-form {\r\n  width: 100%;\r\n  max-width: 600px;\r\n}\r\n\r\n.search-input-group {\r\n  display: flex;\r\n  gap: 0.5rem;\r\n  background: var(--card-bg);\r\n  padding: 0.5rem;\r\n  border-radius: 50px;\r\n  box-shadow: 0 4px 6px var(--card-shadow);\r\n  backdrop-filter: blur(10px);\r\n}\r\n\r\n.search-input {\r\n  flex: 1;\r\n  border: none;\r\n  background: transparent;\r\n  padding: 0.75rem 1.25rem;\r\n  font-size: 1rem;\r\n  color: var(--text-primary);\r\n  outline: none;\r\n}\r\n\r\n.search-input::placeholder {\r\n  color: var(--text-secondary);\r\n  opacity: 0.7;\r\n}\r\n\r\n.search-btn {\r\n  background: var(--accent-color);\r\n  color: white;\r\n  border: none;\r\n  padding: 0.75rem 2rem;\r\n  border-radius: 50px;\r\n  font-size: 1rem;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.search-btn:hover {\r\n  background: var(--accent-hover);\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.4);\r\n}\r\n\r\n.search-btn:active {\r\n  transform: translateY(0);\r\n}\r\n\r\n/* ==================== TEMPERATURE TOGGLE ==================== */\r\n.temp-toggle {\r\n  display: flex;\r\n  gap: 0.5rem;\r\n  background: var(--card-bg);\r\n  padding: 0.5rem;\r\n  border-radius: 50px;\r\n  box-shadow: 0 4px 6px var(--card-shadow);\r\n  backdrop-filter: blur(10px);\r\n}\r\n\r\n.toggle-btn {\r\n  padding: 0.5rem 1.5rem;\r\n  border: 2px solid transparent;\r\n  background: transparent;\r\n  color: var(--text-secondary);\r\n  font-size: 1rem;\r\n  font-weight: 600;\r\n  border-radius: 50px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.toggle-btn.active {\r\n  background: var(--accent-color);\r\n  color: white;\r\n  border-color: var(--accent-color);\r\n}\r\n\r\n.toggle-btn:not(.active):hover {\r\n  background: rgba(245, 158, 11, 0.1);\r\n  border-color: var(--accent-color);\r\n}\r\n\r\n/* ==================== LOADING SPINNER ==================== */\r\n.loading-spinner {\r\n  display: none;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 1rem;\r\n  padding: 3rem;\r\n  color: var(--text-primary);\r\n}\r\n\r\n.loading-spinner.active {\r\n  display: flex;\r\n}\r\n\r\n.spinner {\r\n  width: 60px;\r\n  height: 60px;\r\n  border: 4px solid var(--border-color);\r\n  border-top: 4px solid var(--accent-color);\r\n  border-radius: 50%;\r\n  animation: spin 1s linear infinite;\r\n}\r\n\r\n@keyframes spin {\r\n  0% {\r\n    transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n\r\n/* ==================== ERROR MESSAGE ==================== */\r\n.error-message {\r\n  display: none;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  gap: 1rem;\r\n  padding: 2rem;\r\n  background: var(--card-bg);\r\n  border-radius: 20px;\r\n  box-shadow: 0 10px 30px var(--card-shadow);\r\n  backdrop-filter: blur(10px);\r\n  max-width: 500px;\r\n  margin: 2rem auto;\r\n  text-align: center;\r\n}\r\n\r\n.error-message.active {\r\n  display: flex;\r\n}\r\n\r\n.error-message i {\r\n  font-size: 3rem;\r\n  color: var(--error-color);\r\n}\r\n\r\n.error-text {\r\n  color: var(--text-primary);\r\n  font-size: 1.1rem;\r\n}\r\n\r\n.retry-btn {\r\n  background: var(--accent-color);\r\n  color: white;\r\n  border: none;\r\n  padding: 0.75rem 2rem;\r\n  border-radius: 50px;\r\n  font-size: 1rem;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.retry-btn:hover {\r\n  background: var(--accent-hover);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n/* ==================== WEATHER DISPLAY ==================== */\r\n.weather-display {\r\n  display: none;\r\n  gap: 2rem;\r\n  flex: 1;\r\n}\r\n\r\n.weather-display.active {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n/* ==================== CURRENT WEATHER ==================== */\r\n.current-weather {\r\n  background: var(--card-bg);\r\n  padding: 2rem;\r\n  border-radius: 20px;\r\n  box-shadow: 0 10px 30px var(--card-shadow);\r\n  backdrop-filter: blur(10px);\r\n  border: 1px solid var(--border-color);\r\n}\r\n\r\n.location-info {\r\n  text-align: center;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.location-name {\r\n  font-size: 2rem;\r\n  font-weight: 700;\r\n  color: var(--text-primary);\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.current-date {\r\n  font-size: 1rem;\r\n  color: var(--text-secondary);\r\n}\r\n\r\n.current-conditions {\r\n  text-align: center;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.temp-display {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 1rem;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.weather-icon {\r\n  font-size: 5rem;\r\n  color: var(--accent-color);\r\n}\r\n\r\n.temp-info {\r\n  display: flex;\r\n  align-items: flex-start;\r\n}\r\n\r\n.current-temp {\r\n  font-size: 5rem;\r\n  font-weight: 700;\r\n  color: var(--text-primary);\r\n  line-height: 1;\r\n}\r\n\r\n.temp-unit {\r\n  font-size: 2rem;\r\n  color: var(--text-secondary);\r\n  margin-left: 0.25rem;\r\n}\r\n\r\n.weather-description {\r\n  font-size: 1.5rem;\r\n  color: var(--text-primary);\r\n  margin-bottom: 0.5rem;\r\n  font-weight: 500;\r\n}\r\n\r\n.feels-like {\r\n  font-size: 1rem;\r\n  color: var(--text-secondary);\r\n}\r\n\r\n/* ==================== WEATHER DETAILS GRID ==================== */\r\n.weather-details {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));\r\n  gap: 1.5rem;\r\n  margin-top: 2rem;\r\n  padding-top: 2rem;\r\n  border-top: 2px solid var(--border-color);\r\n}\r\n\r\n.detail-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.detail-item i {\r\n  font-size: 2rem;\r\n  color: var(--accent-color);\r\n}\r\n\r\n.detail-info {\r\n  flex: 1;\r\n}\r\n\r\n.detail-label {\r\n  font-size: 0.875rem;\r\n  color: var(--text-secondary);\r\n  margin-bottom: 0.25rem;\r\n}\r\n\r\n.detail-value {\r\n  font-size: 1.25rem;\r\n  font-weight: 600;\r\n  color: var(--text-primary);\r\n}\r\n\r\n/* ==================== FORECAST SECTION ==================== */\r\n.forecast-section {\r\n  background: var(--card-bg);\r\n  padding: 2rem;\r\n  border-radi